@import url('https://fonts.googleapis.com/css2?family=Gochi+Hand&family=Montserrat:wght@400;700&display=swap');

html {
    box-sizing: border-box;
    padding: 0;
    margin: 0;
}
body {
    font-family: 'Montserrat', sans-serif;
}
h1 {
    text-align: center;
    font-family:'Gochi Hand', cursive;
}
#countries {
    width: 98%;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    gap: 10px;
    flex-grow: 0;
    background-color: #f7f3f3;
    margin-left: auto;
    margin-right: auto;
    border-radius: 4px;
}

li {
    border-radius:4px ;
    list-style: none;
}
#listContainer {
    height: 45px;
    width: 520px;
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    margin-top: 10px;
}
#textContainer {
    display: flex;
    flex-direction: column;
    height: 40px;
    margin: 10px 2px;
    padding: 0 2px;
}
#countryName {
    padding: 0;
    margin: 0;
    font-size: 1em;
    font-weight: bold;
    overflow: hidden;
}
#worldRegion {
    font-size: 0.7em;
    padding: 5px 0;
    margin: 2px 0;
    line-height: 0.04em;
}
#countryPopulation {
    padding: 0;
    margin: 5px 2px;
    font-size: 0.6em;
}
