#flag {
    margin: 3px 0;
    padding: 0;
}
#flagCountry {
    width: 60px;
    height: 40px;
    object-fit: cover;
    padding: 2px;
    border: 1px solid lightgray;
}

/*SEARCH PAGE*/

#worldMap {
    display: block;
    width: 40%;
    height: auto;
    margin-left: auto;
    margin-right: auto;
}
#search-form {
    width: 100%;
    text-align: center;
}
#country-card {
    width: 400px;
    height: 150px;
    border: 2px solid gray;
    border-radius: 5px;
    margin: 10px auto;
    padding: 8px;

}